@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700&display=swap);
*{
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Nunito', sans-serif;
}

.App {
  width: 90vw;
  height: 100%;
  min-height: 600px;
  padding: 2em 4em;
  margin-top: 2em;
  border-radius: 10px;
  background-color: #0D324D;
  display: flex;
  flex: 1 1;
  flex-direction: column;
  justify-content: flex-start;
  align-self: flex-start;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}
.searchbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    width: 100%;
}

.search-container {
    background-color: #33556d;
    width: 50%;
    border-radius: 50px;
    display: flex;
    padding: .6em;
}

.search-container i {
    font-size: 20px;
    color: #0D324D;
    cursor: pointer;
}

.searchInput {
    flex: 1 1;
    background-color: transparent;
    border: none;
    outline: none;
    color: #fff;
    font-family: 'Nunito', sans-serif;
    font-size: 16px;
}

.searchInput::-webkit-input-placeholder {
    color: #aaa;
    
}

.searchInput:-ms-input-placeholder {
    color: #aaa;
    
}

.searchInput::-ms-input-placeholder {
    color: #aaa;
    
}

.searchInput::placeholder {
    color: #aaa;
    
}

.searchbar h1 {
    color: #fff;
}


.profile-container {
    margin-top: 1em;
    display: flex;
    flex-direction: row;
    justify-self: center;
    align-items: center;
}

.profile {
    flex: .30 1;
    background-color: #0cbaba;
    background-image: linear-gradient(350deg, #0cbaba 0%, #380036 74%);
    display: flex;
    flex-direction: column;
    padding: 1em;
    border-radius: 10px;
    text-align: center;
    max-width: 360px;
    overflow: hidden;
    align-self: flex-start;
}

.profile-info h1 {
    color: #fff;
    margin-top: 20px;
    font-size: 22px;
}

.user-pic {
    width: 100%;
    height: auto;
    max-width: 180px;
    border: 14px solid #7F5A83;
    border-radius: 50%;
    margin: 0 auto;
    margin-top: 1em;
}

.job-title {
    color: #aaa;
    margin-top: -14px;
}

.description {
    margin-top: 2em;
    font-size: 15px;
    color: #aaa;
    overflow: hidden;
    max-width: 100%;
    max-height: 100px;
    text-overflow: ellipsis;
}

.stats {
    display: flex;
    flex: 1 1;
    justify-content: space-evenly;
    flex-direction: row;
    color: #aaa;
    margin-top: 1em;
}
.stat span{
    display: inline-block;
    margin-left: .5em;
}

.more {
    flex: 1 1;
    background-color: #380036;
    height: 100%;
    margin-left: 2em;
    border-radius: 10px;
    padding: 1em 0;
    color: #fff;
    align-self: flex-start;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: transparent; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #129FA7; 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #129FA7; 
  }

.more h1 {
    color: #ccc;
    padding-left: 1em;
}

.more ul {
    list-style: none;
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    padding-left: 2em;
    padding-right: 2em;
    overflow: hidden;
    overflow-y: auto;
    flex: 1 1;
    max-height: 450px;
}

.more ul li {
    display: block;
}

.more ul li a {
    display: block;
    text-decoration: none;
    color: #aaa;
    border-bottom: 1px solid #0D324D;
    margin-bottom: 1em;
    cursor: pointer;
    padding: .5em;
    transition: 1s all;
}

.more ul li a:hover {
    background-color: #0D324D;
}

.more ul li a p {
    font-size: 14px;
    margin: 0;
}

.more .repoDesc {
    font-size: 12px;
}

.no-user-info-container {
    display: flex;
    flex-direction: column;
    color: #aaa;
    justify-content: center;
    align-items: center;
    flex: 1 1;
}

.no-user-info-container {
    align-self: center;
}
